import './App.css';
import Navbar from './Component/Navbar'
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
import { useState } from 'react';
import Login from './Component/Login';
import Alert from './Component/Alert';
import Signup from './Component/Signup';
import Content from './Component/Content';
import Navbarlogin from './Component/Navbarlogin';
import Contentlogin from './Component/Contentlogin';
import './db'
import Chat from './Component/Chat';
import Chatn from './Component/Chatn';

function App() {

  const[alert,setalert] = useState(null);

  const showalert = (message,type)=>{
    setalert({
    message : message,
    type : type
  })
  setTimeout(() => {
    setalert(null);
  }, 1500);
}

  return (
    <div>
      <Router>
      <Routes>
      <Route exact  path="/" element={<><Navbar/><Content/></>} />
      <Route exact  path="/chatbot" element={<><Navbar/><Content/></>} />
      <Route exact  path="/chatn" element={<><Navbar/><Chatn/></>} />
      <Route exact  path="/chat" element={<><Navbarlogin/><Chat showalert={showalert}/><Alert alert={alert}/></>} />
      {/* <Route exact  path="/login" element={<Login/>} />
      <Route exact  path="/signup" element={<Signup/>} /> */}
      <Route exact  path="/login" element={<><Alert alert={alert}/><Login showalert= {showalert}/></>} />
      <Route exact  path="/user" element={<><Navbarlogin showalert={showalert}/><Alert alert={alert}/><Contentlogin showalert={showalert}/></>} />
      <Route exact  path="/signup" element={<><Alert alert={alert}/><Signup showalert={showalert}/></>} />
      </Routes>
      </Router>
    </div>
  );
}

export default App;
