import React, { useState } from 'react';
import './Contenet.css';
// import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';





function Content(props) {
  // const [micon, setMicon] = useState("off");
  const [text, setText] = useState("");
  const [data, setData] = useState("");

  let emaill = localStorage.getItem('useremail');

  const [chat , setchat] = useState({ email: emaill , chat: "Default Chat"});

  // const toggleMic = () => {
  //   setMicon(prevState => (prevState === "off" ? "on" : "off"));
  // };

  const handleChange = (e) => {
    setText(e.target.value);
    console.log(text);

  };

  let filee;

  const handleFileChange = (e) => {
    e.preventDefault();
    const file = e.target.files[0];
    filee = file;
    console.log(file);
    console.log(localStorage.getItem('useremail'));
    console.log(emaill);
  };

  const transcribe = async (e) => {
    const formData = new FormData();
    formData.append("file", filee);
    formData.append("model", "whisper-1");

    const response = await fetch("https://api.openai.com/v1/audio/transcriptions", {
      method: "POST",
      headers: {
        Authorization: "Bearer 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"
      },
      body: formData
    });

    const dataa = await response.json();
    console.log(dataa);
    setData(data.text);
    console.log(data);

    setchat({email: emaill, chat : dataa.text})

  }



  const submit = async(e)=>{
    e.preventDefault();
    const responce = await fetch("http://localhost:5000/api/auth/chat",{
      method:'POST',
      headers : {
        'Content-Type' : 'application/json'
      },

      body : JSON.stringify({ email: emaill , chat: chat.chat})

  });


  const json = await responce.json()
  console.log(json);

  if(json.success){
    props.showalert("Chat Saved " , 'success');
  }
  else{
    props.showalert("Chat not saved" , 'danger')
  }

  }






  const submitt = async(e)=>{
    e.preventDefault();
    const responce = await fetch("http://localhost:5000/api/auth/chat",{
      method:'POST',
      headers : {
        'Content-Type' : 'application/json'
      },

      body : JSON.stringify({ email: emaill , chat: text})

  });


  const json = await responce.json()
  console.log(json);

  if(json.success){
    props.showalert("Chat Saved " , 'success');
  }
  else{
    props.showalert("Chat not saved" , 'danger')
  }

  }


  const speech = () => {
    let msg = new SpeechSynthesisUtterance();
    msg.text = text;
    window.speechSynthesis.speak(msg);
  }
    
  






  return (
    <>
      <div className='content'>
        <div className="speecttotext">
          <h1>Speech to text</h1>
          <input type="file" onChange={handleFileChange} />
          <button onClick={transcribe}>Submit</button>
          <button className='chat' onClick={submit}>Save Chat</button>
        </div>

  <div className='line'>

  </div>

        <div className="textarea">
        <h1>Text to Speech</h1>
          <textarea type="text" rows={4} cols={50} onChange={handleChange} />

          <div className={`btndiv`}>
          <div className={`btndiv`} onClick={speech}>
          {/* <div className={`btndiv`}> */}
            <PlayArrowIcon className={`iconplay` } />
          </div>
          <button className='chat' onClick={submitt}>Save Chat</button>
          </div>
        </div>

        {/* <div className={`chaticon ${micon}`} onClick={toggleMic}>
          <KeyboardVoiceIcon className='icon' />
        </div> */}
      </div>

      <div className="output">
        <h3>Audio Text</h3>
        <p className="output">{data}</p>
      </div>
    </>
  )
}

export default Content;
