import {React , state , useState , useEffect} from 'react'
import ChatIcon from '@mui/icons-material/Chat';
import './Chat.css'
import CloseIcon from '@mui/icons-material/Close';
import SendIcon from '@mui/icons-material/Send';
import { useSpeechContext } from '@speechly/react-client';
import { PushToTalkButton , PushToTalkButtonContainer  } from '@speechly/react-ui';
import Message from './Message';
import BookmarkIcon from '@mui/icons-material/Bookmark';

function Chat(props) {

    // const [chatbot , setchatbot] = useState("off");
    const [chaticonhide , setcih] = useState("show");
    const [message , setmessage] = useState(null);
    const [outmessage , setoutmessage] = useState('');
    const { segment, listening, attachMicrophone, start, stop } = useSpeechContext();
    const [transcripts, setTranscripts] = useState('');
    const [tentative, setTentative] = useState('');
    
    const messagee = [
      {
        "role": "user",
        "message": "Hi",
      },
      {
        "role": "assistant",
        "message": "Hi, How can i help you",
        "data" : new Date()
      }
    ];
    
    const [chat , setchat] = useState(messagee);


    const addmessage = (role , msge) =>{
      const msg = {
        "role" : role,
        "message" : msge,
        "data" : new Date()
      }
      setchat(chat.concat(msg));
    }
    
    useEffect(() => {
      // console.log(chat);
    if (segment) {
      const transcript = segment.words.map((word) => word.value).join(' ');
      setTentative(transcript);
      if (segment.isFinal) {
        setTentative('');
        setTranscripts((current) => [...current, segment]);
      }
      setmessage(segment.words.map((word) => word.value).join(' '));
      
    }
  }, [chat , segment]);


    // const ison = ()=>{
    //     if(chatbot==="off"){
    //         setchatbot("on");
    //     }
    //     else{
    //         setchatbot("off");
    //     }
    // }

    const cicon = ()=>{
        if(chaticonhide==="show"){
            setcih("hide");
        }
        else{
            setcih("show");
        }
    }

    const getmessage = (e) =>{
      if(segment){
        setmessage(segment.words.map((word) => word.value).join(' '));
      }
      else{
        setmessage(e.target.value);
      }
      // addmessage('user' , message);
    }


    // onKeyDownHandler = e => {
    //   if (e.keyCode === 13) {
    //     this.sendMessage();
    //   }
    // };
    


    // const handleClick = async () => {
    //   if (listening) {
    //     await stop();
    //   } else {
    //     await attachMicrophone();
    //     await start();
    //   }
    // };

    const speech = (text) => {
      let msg = new SpeechSynthesisUtterance();
      msg.text = text;
      window.speechSynthesis.speak(msg);
    }

    const save = ()=>{
      console.log("Pressed");
      addmessage('user' , message);
    }


    let emaill = localStorage.getItem('useremail');



    const transcribe = async (e) => {

      const url = "https://api.openai.com/v1/chat/completions"; 

      const options = {
        method: 'POST',
        headers: {
          Authorization: "Bearer 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",
          'Content-Type' : 'application/json'
        },
        body : JSON.stringify(
          {
            model: "gpt-3.5-turbo",
            messages: [{role: "user", content: message}],
          }
          )
        };
        
        try{
          
          const response = await fetch(url , options);
          
          const dataa = await response.json();
          // console.log(dataa);
          setoutmessage(dataa.choices[0].message.content);
          console.log(dataa.choices[0].message.role);
          console.log(dataa.choices[0].message.content);
          
          addmessage(dataa.choices[0].message.role , dataa.choices[0].message.content);
          speech(dataa.choices[0].message.content);
          console.log(messagee);
          
        }
        catch{
            console.log("Api not working");
        }
    
      }

      const savechat = async (e)=>{
        e.preventDefault();
        const responce = await fetch("http://localhost:5000/api/auth/userchat",{
          method:'POST',
          headers : {
            'Content-Type' : 'application/json'
          },

          body : JSON.stringify({ email: emaill , chat: chat})

      });


      const json = await responce.json()
      console.log(json);

      if(json.success){
        props.showalert("Chat Saved " , 'success');
      }
      else{
        props.showalert("Chat not saved" , 'danger')
      }
      }


    
  return (
    <div className='body'>
      <div className={`chaticon ${chaticonhide}`} onClick={cicon}>
        <ChatIcon className='iconchat'/>
      </div>


      <div className={`chatbox ${chaticonhide==="hide"?"show":"hide"}`}>


      


        <div className="top">
            <div className="h">ChatBot</div>
            <div className="cut"  onClick={cicon}><CloseIcon/></div>
        </div>


        <div className="middle">

          {/* <div className='messageu message'>
            {segment ? (
              <>
              {segment.words.map((e)=>e.value).join(" ")}
              </>
            ) : null}
          </div> */}

        {chat.map((msg)=>{
            return <Message m={msg}/>
        })} 

          

        </div>

        <div className="buttom">
        <button className='btn' onClick={savechat}>Save Chat</button>

          <div className="inp">
            <input type="text" onChange={getmessage} value={message?message:" "} />
          </div>

          <div className="send" onClick={transcribe}>
            <SendIcon/>
          </div>
          <div className="sendd" onClick={save}>
            <BookmarkIcon/>
          </div>

          <div >
            <PushToTalkButtonContainer>
              <PushToTalkButton />
            </PushToTalkButtonContainer>
          </div>

        </div>

      </div>

    </div>
  )
}

export default Chat
