import { React , useEffect} from 'react'
import './Navbar.css'
import {Link } from 'react-router-dom'

import { useNavigate } from 'react-router-dom'

function Navbarlogin(props) {

  const navigate = useNavigate();


  useEffect(() => {
    if(!localStorage.getItem('token')){
      navigate('/');
      props.showalert("Login first" , 'success');
    }
  })
  

  const handleonclick = (e)=>{
    localStorage.removeItem('token');
    localStorage.removeItem('username');
    localStorage.removeItem('useremail');
    navigate('/');
  }


    let username = localStorage.getItem('username');
    // let useremail = localStorage.getItem('useremail');




  return (
    <div className="navbar">
      <div className="navfirst">
        <ul className="ul">
            <Link to="/"><li className="li name" >ChatBot</li></Link>
            <Link to="/chat"><li className="li name" >Chat</li></Link>
        </ul>
      </div>


<div className="navsecond">
        <ul className="navsecondul">
          <div className='avatar'>{username}</div>
            <button onClick={handleonclick} className='buttonsign'>Sign out</button>
        </ul>
      </div>
      </div>
  )
}

export default Navbarlogin 
